.yc-label {
  display: inline-flex;
  align-items: center;
  position: relative;
  transition-property: opacity, color, background-color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
.yc-label__text {
  width: 100%;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.yc-label .yc-label__addon {
  display: flex;
  align-items: center;
  justify-content: center;
}
.yc-label .yc-label__addon_side_left, .yc-label .yc-label__addon_side_right {
  position: absolute;
  top: 0;
}
.yc-label .yc-label__addon_side_left {
  left: 2px;
}
.yc-label .yc-label__addon_side_right {
  right: 0;
}
.yc-label .yc-label__addon_interactive {
  --yc-button-background-color: transparent;
  cursor: pointer;
  color: inherit;
  transition: color, background-color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}
.yc-label_size_xs {
  height: 20px;
}
.yc-label_size_xs .yc-label__text {
  line-height: 20px;
  margin: 0 8px;
}
.yc-label_size_xs .yc-label__addon {
  width: 20px;
  height: 20px;
}
.yc-label_size_xs.yc-label_has-right-addon .yc-label__text {
  margin-right: 22px;
}
.yc-label_size_xs.yc-label_has-left-addon .yc-label__text {
  margin-left: 24px;
}
.yc-label_size_xs.yc-label_style_default, .yc-label_size_xs.yc-label_style_rounded {
  border-radius: var(--yc-border-radius-xs);
}
.yc-label_size_xs.yc-label_style_default .yc-label__addon_interactive, .yc-label_size_xs.yc-label_style_rounded .yc-label__addon_interactive {
  --yc-button-height: 20px;
  --yc-button-border-radius: var(--yc-border-radius-xs);
}
